body, html {
  height: 100%;
  margin: 0;
  width:device-width;
}

#root {
  text-align: center;
  background-image: url('./imgs/background.jpeg');
 
  
  background-size: 100%;
  
  background-repeat: repeat;
  background-size: cover;
  height: 100%;
  

}
.myBadge
{
  width: 150px;
  height: 40px;
  text-align: center;
}
.myBtn
{
  width: 50px;
  height: 40px;
  text-align: center;
  line-height: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
